<!--档案日志管理-->
<template>
	<div ref="table" class="workBench">
    <el-container  :style="{'height':siteContentViewHeight +'px'}">
			<el-main ref="elMain">
				<el-tabs v-model="activeName" @tab-click="handleClick"  type="card">
					<el-tab-pane label="登录日志" name="first" :key="'first'">
						<loginLogManagement v-if="isChildUpdate1" :siteContentViewHeight="siteContentViewHeight"></loginLogManagement>
					</el-tab-pane>
					<el-tab-pane label="操作日志" name="last" :key="'last'">
						<operationLog v-if="isChildUpdate2" :siteContentViewHeight="siteContentViewHeight"></operationLog>
					</el-tab-pane>
				</el-tabs>
			</el-main>
    </el-container>
		</div>

</template>
<script>
    import loginLogManagement from '../loginLogManagement/index.vue';
    import operationLog from '../operation-log/index.vue';
	export default {
		name: "system-log-management-index",
    props:['siteContentViewHeight'],
		data() {
			return {
				activeName: "first",
				isChildUpdate1:true,
				isChildUpdate2:false,
			};
		},
		components: {
            loginLogManagement,
            operationLog
		},
		created() {

        },
		methods: {
			handleClick(tab) {
				if(tab.name == "first") {
					this.isChildUpdate1 = true;
					this.isChildUpdate2 = false;
				} else if(tab.name == "last") {
					this.isChildUpdate1 = false;
					this.isChildUpdate2 = true;
				}
        	}
		},
	};
</script>
<style lang="scss" scoped>
  >>> .el-main{
    padding-bottom: 0;
  }
</style>



