<template>
	<el-container class="systemLog-management-container">
		<el-header class="my-header" style="height: 45px;">系统日志管理</el-header>
		<el-main>
			<div class="systemLog-management-title">
				<div class="systemLog-management-title-left">
					<!-- 操作状态：
					<el-select v-model="status" placeholder="请选择" clearable>
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
					操作人：
					<el-input v-model="uCreator" clearable placeholder="请输入操作人"></el-input>
					IP：
					<el-input v-model="ip" clearable placeholder="请输入IP地址"></el-input> -->
					<el-date-picker v-model="data" type="daterange" format="yyyyMMdd" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
					</el-date-picker>
					<el-button type='primary' @click="search">查询</el-button>
				</div>
			</div>
			<!--表格-->
			<el-table ref="table" :data="systemLogList" style="width: 100%;" :height="siteContentViewHeight - 155 +'px'">
				<el-table-column label="序号" align="center" min-width="6%">
					<template slot-scope="scope"> {{pageObj.pageIndex*pageObj.pageSize+scope.$index+1}} </template>
				</el-table-column>
				<el-table-column prop="userCode" label="登录人账号" show-overflow-tooltip  min-width="10%">
				</el-table-column>
				<el-table-column prop="userName" label="登录人姓名" min-width="10%">
				</el-table-column>
				<el-table-column prop="logResult" label="登录结果" show-overflow-tooltip min-width="5%">
				</el-table-column>
				<el-table-column prop="logMessage" label="登录失败原因" show-overflow-tooltip min-width="15%">
				</el-table-column>
				<el-table-column prop="logIp" label="登录IP地址" min-width="15%">
				</el-table-column>
				<!-- <el-table-column prop="logMac" label="登录mac地址" min-width="15%">
				</el-table-column> -->
				<el-table-column prop="logTime" label="时间" min-width="15%">
				</el-table-column>
			</el-table>
			<!--分页-->
			<div>
				<pagination :pageIndex="pageObj.pageIndex" :total="pageObj.total" :pageSize="pageObj.pageSize" @PageTurning="PageTurning"></pagination>
			</div>
		</el-main>
	</el-container>
</template>

<script>
	import pagination from '../../../components/baseComponents/Pagination.vue';
	export default {
		name: "SystemLogManagement", //系统日志管理
		components: {
			pagination
		},
    props:['siteContentViewHeight'],
		data() {
			return {
				pageObj: {
                   pageIndex: this.API.constObj.pageIndex, //当前页码
                   // pageIndex: 0, //当前页码
					total: 0, //数据总数
                   pageSize: this.API.constObj.pageSize, //页大小
                    //pageSize: 10, //页大小
				},
				systemLogList: [], //系统日志集合
				status: "", //操作状态
				uCreator: "", //操作人
				ip: "", //ip
				data: "", //时间
				startDateStr: "", //开始时间
				endDateStr: "", //结束时间
				options: [{
					value: '0',
					label: '异常'
				}, {
					value: '1',
					label: '成功'
				}],
				tableHeight: "400",//给tabale设置高度
			}
		},
		created() {
			this.findSystemLogPage();
		},
		watch:{
			systemLogList:{
				handler:function(newVal){
					this.tableHeight = window.innerHeight - this.$refs.table.$el.offsetTop - 28 - 165;
				}
			}
		},
		methods: {
			handleCurrentChange(val) { // 分页导航
				this.pageObj.pageIndex = val;
				this.findSystemLogPage();
			},
			//分页组件回调方法
			PageTurning(page_obj) {
				console.log("分页组件传回的分页信息：" + JSON.stringify(page_obj));
				this.pageObj.pageIndex = page_obj.page_index;
				this.pageObj.pageSize = page_obj.page_size;
				this.findSystemLogPage();
			},
			findSystemLogPage() { //系统日志列表查询
					 this.$http({
						url: this.$http.adornUrl(this.API.queryOperateLog),
						method: "post",
						data: this.$http.adornData({
							// status: this.status,
							// ip: this.ip,
							// uCreator: this.uCreator,
							startDate: this.startDateStr,
							endDate: this.endDateStr,
							pageIndex: this.pageObj.pageIndex,
							pageSize: this.pageObj.pageSize
						})
						}).then(({ data }) => {
							if(data.code == "0") {
								this.systemLogList = data.data.records;
								this.pageObj.total = data.data.total;
							} else {
								this.$message({
									type: 'error',
									message: '获取数据失败，请刷新重试！'
								});
							}
						}).catch(function(err) {
							console.log("连接错误" + err);
				})
			},
			search() { //搜索
				if(this.data === null){
					this.startDateStr = "";
					this.endDateStr = "";
				}else if((this.data || this.data != "") && this.data.length > 0){
					let strDate = this.data;
					this.startDateStr = this.commonFun.dateFormat(strDate[0], "yyyyMMdd");
					this.endDateStr = this.commonFun.dateFormat(strDate[1], "yyyyMMdd");
				}else{
					this.startDateStr = "";
					this.endDateStr = "";
				}
				// this.startDateStr = this.commonFun.dateFormat(strDate[0], "yyyyMMdd");
				// this.endDateStr = this.commonFun.dateFormat(strDate[1], "yyyyMMdd");
				this.pageObj.pageIndex = this.API.constObj.pageIndex;
				this.findSystemLogPage();
			},
		}
	}
</script>

<style lang="scss" scoped>
	.systemLog-management-container {
		.my-header {
			padding-top: 15px;
			display:none;
		}
		.el-main {
			background-color: #fff;
			padding:0 !important;
			.systemLog-management-title {
				display: flex;
				justify-content: space-between;
				margin: 10px 10px;
				.el-select {
					width: 180px;
					margin-right: 10px;
				}
				.el-date-editor--daterange.el-input__inner{
					width:400px;
				}
				.el-input {
					width: 180px;
					margin-right: 10px;
				}
				.el-button{
					margin: 0 0 0 10px;
				}
			}
			.material_page {
				margin-top: 10px;
				text-align: right;
			}
		}
	}
  >>> .el-main{
    padding-bottom: 0;
  }
</style>



